
.navbar-menu__botao {
    cursor: pointer;
    letter-spacing: 2px;
    text-transform: uppercase;
    padding: 0.5rem;
    border: 3px solid #FF6018;
    border-radius: 5px;
  }
  
  .navbar-menu__botao--aberto:after {
    text-transform: uppercase;
    position: fixed;
    top: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.5);
  }
  
  .navbar-menu__opcoes {
    list-style: none;
    color: #FF6018;
    text-transform: uppercase;
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 80vw;
    padding: 2rem 1rem 0;
    margin: 0;
    background:#FF6018;
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 1s ease-in-out;
    transition: -webkit-transform 1s ease-in-out;
    transition: transform 1s ease-in-out;
    transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
  }
  
  .navbar-menu__opcoes--aberto {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  
  .navbar-menu__opcoes li {
    cursor: pointer;
  }
  
  .navbar-menu__opcoes li + li {
    margin-top: 1rem;
  }
  
  @media (min-width: 992px) {
    .navbar-menu__botao {
        display: none;
    }
  
    .navbar-menu__opcoes {
        display: flex;
        align-items: center;
        position: static;
        position: initial;
        height: auto;
        height: initial;
        width: auto;
        width: initial;
        padding: 0;
        padding: initial;
        background: transparent none repeat 0 0 / auto auto padding-box border-box scroll;
        background: initial;
        -webkit-transform: none;
                transform: none;
        -webkit-transform: initial;
                transform: initial;
        -webkit-transition: none 0s ease 0s;
        transition: none 0s ease 0s;
        -webkit-transition: initial;
        transition: initial;
    }
  
    .navbar-menu__opcoes--aberto {
        -webkit-transform: none;
                transform: none;
        -webkit-transform: initial;
                transform: initial;
    }
  
    .navbar-menu__opcoes--ativo {
        border-bottom: 2px solid#FF6018;
    }
  
    .navbar-menu__opcoes li + li {
        margin-top: 0;
        margin-left: 1rem;
    }
  
    .navbar-menu__opcoes li a {
        color: #FF6018;
        text-transform: uppercase;
        text-decoration: none;
        padding: 0.3rem 0;
    }
  
    .navbar-menu__opcoes li a:hover {
      color:#FF6018;
      -webkit-transition: all 0.3s ease;
      transition: all 0.3s ease;
    }
    
  }
.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex: 0 1 60px;
    min-height: 80px;
    padding: 0;
    color:rgb(129, 67, 38);
    background-color:black;
    position: fixed; 
    top: 0; 
    width: 100%; 
    z-index: 99;
  }
  
  .navbar-logo {
    display: block;
    width: 100px;
  }
  
  .navbar-links {
    list-style: none;
    display: flex;
    align-items: center;
    padding: 0;
  }
  
  .navbar-links li {
    margin-left: 1rem;
  }
  
  .navbar-links a {
    color:black;
    text-transform: uppercase;
    padding-bottom: 0.3rem;
  }
  
  .navbar-links__ativo {
    border-bottom: 2px solid black;
  }
